li {
  padding: 15px 0px;
  border-bottom: 1px solid #fff;
  padding-bottom: 35px;
}

.SideBar {
  margin-block: 30px;
  background-color: rgb(5, 6, 33);
  color: #fff;
  padding: 20px 30px;
  margin-inline: 10px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}
.body {
  cursor: pointer;
}
.body:hover > .body-title {
  color: #d7db19;
}
ul :nth-child(3) {
  border-bottom: none;
}

.side-title {
  color: #d7db19;
}

@media only screen and (min-width: 1000px) {
  .SideBar {
    margin-top: 10px;
  }
}
