.Card {
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
  padding-block: 25px;
}
img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  display: block;
}
span {
  display: block;
}

.card-img-container {
  width: 180px;
  height: 140px;
}

.card-container {
  display: flex;
  gap: 10px;
}

.card-number {
  color: #999;
  margin-block: 10px;
}
.card-title {
  margin-block: 5px;
}
.card-description {
  color: #999;
}
.card-body:hover > .card-title {
  color: #f67373;
}
.card-body {
  cursor: pointer;
}
