* {
  font-family: "Inter Tight", sans-serif;
}
.HeroSection {
  padding-block: 10px;
}
ul {
  list-style: none;
  padding: 0px;
}

@media only screen and (min-width: 1000px) {
  .app-wrap {
    display: flex;
    z-index: -10;
  }

  /* SideBar is a head class for the SideBar component */
  .SideBar {
    width: 60%;
  }
  .App {
    padding-inline: 70px;
  }
}
